.gallery {
  position: relative;
  margin: 52px auto 0;

  &__row {
    display: flex;
  }

  &__text {
    margin: 24px 0 40px 10px;
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 27px;
  }

  &__text-main {
    margin-bottom: 61px;
  }

  &__button {
    display: block;
    margin: 0 auto;
    color: #0075ff;
    background-color: #fff;
    width: 188px;
    box-shadow: none;
  }
}

.image {
  border-radius: 8px;
  margin: 0 10px;
  height: 256px;
  object-fit: cover;

  &__normal {
    width: calc(50% - 20px);
  }

  &__1-6 {
    width: calc(65% - 20px);
  }

  &__2-5 {
    width: calc(35% - 20px);
  }
}

@media screen and (max-width: 320px) {
  .gallery {
    width: 100%;
    margin: 0;
  }

  .image {
    height: 131px;

    &__normal {
      width: calc(50% - 20px);
    }

    &__1-6 {
      width: calc(50% - 20px);
    }

    &__2-5 {
      width: calc(50% - 20px);
    }
  }
}
